<template>

    <div>
        <ul class="myList">
            <van-list
                    v-model="loading"
                    :finished="finished"
                    finished-text="没有更多了"
                    @load="getList"
            >
            <li v-for="(item,index) in listData">
                <router-link :to="{path:'/redchat', query:{groupId:item.redgroup_id, order:index+1, fuid:uid, type:type == 1 ? 'like' : 'create'}}"><p>{{item.name}}（{{item.user_count}}）</p><span></span>
                </router-link>
            </li>
            </van-list>
        </ul>
    </div>
</template>

<script>
    import { userGroup } from '@/api/red'
    import Toast from 'vant/lib/toast';
    import 'vant/lib/toast/style';
    import { List } from 'vant';
    import 'vant/lib/list/style';

    export default {
        name: "GroupList",
        components: {
            [List.name]: List,
        },
        props:['uid', 'type'],
        data()
        {
            return {

                page:1,
                // 收藏群列表
                listData:[],
                loading:false,
                finished:false

            }
        },
        mounted()
        {
            this.getList()
        },
        methods:{

            // 获取用户信息
            getList()
            {
                this.loading = true
                userGroup({uid:this.uid, page:this.page, type:this.type}).then(res => {
                    this.loading = false
                    this.page++
                    if (res.length < 20)
                    {
                        this.finished = true
                    }
                    this.listData = this.listData.concat(res)
                })
            }

        }
    }
</script>

<style scoped>
    .myList li {
        height: 0.5rem;
        line-height: 0.5rem;
        position: relative;
    }
    .myList li a {
        display: block;
        margin-left: 0.15rem;
        padding-right: 0.15rem;
        overflow: hidden;
        border-bottom: 0.01rem solid #f5f5f5;
    }
    .myList li a p {
        float: left;
        font-size: 0.14rem;
        color: #3F3F3F;
    }
    .myList li a span {
        width: 0.12rem;
        height: 0.12rem;
        background: url('../../../static/images/my/btn_more_gray.png') no-repeat center;
        background-size: 100%;
        display: inline-block;
        float: right;
        margin-top: 0.2rem;
    }
</style>



