<template>

    <div class="app">
        <!--  公共头部  -->
        <div class="myHead pr">
            <div class="imgBox">
                <img :src="userInfo.avatar" alt="">
                <span>{{userInfo.nickname}}</span>
            </div>
        </div>
        <ul class="ov nav">
            <li class="fl"><a href="javascript:void (0)" class="pr" :class="{active:navNum == 1}" @click="navNum = 1">ta喜欢的红包群<span></span></a></li>
            <li class="fl"><a href="javascript:void (0)" class="pr" :class="{active:navNum == 2}" @click="navNum = 2">ta创建的红包群<span></span></a></li>
        </ul>
         <group-list :uid="uid" type="1" v-show="navNum == 1"></group-list>
         <group-list :uid="uid" type="2" v-show="navNum == 2"></group-list>
    </div>
</template>

<script>
    import GroupList from "@/page/red/components/groupList"
    import { userGroup } from '@/api/red'
    import { userInfo } from '@/api/user'
    import Toast from 'vant/lib/toast';
    import 'vant/lib/toast/style';
    import { Switch } from 'vant';
    import 'vant/lib/switch/style';

    export default {
        name: "Usergroup",
        components: {
            GroupList,
        },
        data()
        {
            return {

                uid:'',
                navNum:1,
                // 个人信息
                userInfo:{
                    nickname:'',
                    avatar:''
                },
                // 收藏群列表
                collectionData:[],
                // 创建群列表
                mygroupData:[]

            }
        },
        created()
        {
            this.uid = this.$route.query.uid
        },
        mounted()
        {

            this.getUserInfo()
        },
        methods:{

            // 获取用户信息
            getUserInfo()
            {
                userInfo({uid:this.uid}).then(res => {

                   this.userInfo = res
                })
            },



        }
    }
</script>

<style scoped>
    @import "../../static/css/usergroup.css";
</style>



